<template>
  <div class="pb-5">
    <h1 v-if="product.id" class="mb-5">編輯商品</h1>
    <h1 v-else class="mb-5">新增商品</h1>
    <form 
      v-show="!isLoading"
      @submit.prevent.stop="handleSubmit"
    >
      <div class="form-group">
        <label for="name">商品名稱</label>
        <input
          type="text"
          class="form-control"
          value=""
          name="name"
          id="name"
          placeholder="輸入商品名稱"
          required
          v-model="product.name"
        />
      </div>

      <div class="form-group">
        <label for="categoryId">選擇商品分類</label>
        <select
          class="form-control"
          id="CategoryId"
          name="CategoryId"
          v-model="product.categoryId"
        >
          <option value="" selected disabled>選擇類別</option>
          <option
            v-for="category in categories"
            :key="category.id"
            :value="category.id"
          >
            {{ category.name }}
          </option>
        </select>
      </div>

      <div class="form-group">
        <label for="address">設定價格</label>
        <input
          type="text"
          class="form-control"
          value=""
          name="price"
          id="price"
          placeholder="輸入商品價格"
          required
          v-model="product.price"
        />
      </div>

      <div class="form-group">
        <label for="description">商品描述</label>
        <textarea
          class="form-control"
          id="description"
          name="description"
          rows="15"
          placeholder="輸入商品描述..."
          required
          v-model="product.description"
        ></textarea>
      </div>

      <div class="form-group">
        <label for="image">上傳商品圖片</label>
        
        <input
          id="image"
          type="file"
          class="form-control-file"
          name="image"
          accept="image/*"
          @change="handleFileChange"
          multiple
        />
      </div>

      <div v-if="product.image" class="form-group">
        <label for="image">圖片預覽</label>
        <img class="row col-4 mb-3 mr-0 ml-0 pr-0 pl-0 rounded" :src="product.image">
      </div>

      <div class="fonm-group">
      <button 
        type="submit" 
        class="btn btn-primary" 
        :disabled="isProcessing"
      >
        {{isProcessing ? '處理中...' : '送出'}}
      </button>
      </div>

    </form>
  </div>
</template>

<script>
import productsAPI from '../../apis/products'
import { Toast } from '../../utils/helpers'

export default {
  name: 'BackStageForm',
  props: {
    initialProduct: {
      type: Object,
      default: () => {
        return {
          name: '',
          description: '',
          price: '',
          image: ''
        }
      }
    },
    isProcessing: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      categories: [],
      product: {},
      isLoading: true
    }
  },
  methods: {
    async fetchData() {
      try {
        const { data } = await productsAPI.categories.get()
        this.categories = data.categories
        this.product = this.initialProduct
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.toastFire('error', '無法取得分類資訊')
      }
    },
    handleFileChange(e) {
      const { files } = e.target
      if (files.length === 0 ) {
        this.product.image = ''
        return
      }
      const imgURL = window.URL.createObjectURL(files[0])
      console.log('imgURL', imgURL)
      this.product.image = imgURL
    },
    handleSubmit(e){
      if (!this.product.name) {
        this.toastFire('error', '請填寫名稱')
        return
      }
      if (!this.product.categoryId) {
        this.toastFire('error', '請選擇商品分類')
        return
      }
      if (!this.product.price) {
        this.toastFire('error', '請設定價格')
        return
      }
      const form = e.target
      const formData = new FormData(form)
      this.$emit('after-submit', formData)
    },
    toastFire(action, text) {
      Toast.fire({
        icon: action,
        title: text,
        background: '#eee',
        showConfirmButton: false,
      })
    },
  },
  created() {
    this.fetchData()
  },
  watch: {
    initialProduct (newValue) {
      this.product = {
        ...this.product,
        ...newValue
      }
    }
  }
}
</script>
