<template>
  <div class="">
    <Spinner :class="{ 'fade-out': !isLoading }" />
    <BackStageForm
      :initial-product="product"
      @after-submit="handleAfterSubmit"
      :is-processing="isProcessing"
    />
  </div>
</template>

<script>
import BackStageForm from '../components/backstage/BackStageForm'
import productsAPI from '../apis/products'
import Spinner from '../components/Spinner'
import { Toast } from '../utils/helpers'

export default {
  name: 'AdminProductEdit',
  components: {
    BackStageForm,
    Spinner
  },
  data() {
    return {
      product: {
        name: '',
        description: '',
        price: '',
        image: ''
      },
      isProcessing: false,
      isLoading: true
    }
  },
  methods: {
    async fetchData(id) {
      try {
        this.isLoading = true
        const { data } = await productsAPI.products.getDetail({ id })
        this.product = data.product
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.isProcessing = false
        this.isLoading = false
        this.toastFire('error', '取得資料失敗，請稍後再試')
      }
    },
    async handleAfterSubmit(formData) {
      try {
        this.isProcessing = true
        const { data } = await productsAPI.products.update({ id: this.product.id, formData })
        if (data.status !== 'success') {
          throw new Error(data.message)
        }
        this.$router.push({ name: 'adminProducts' })
      } catch (err) {
        console.log(err)
        this.toastFire('error', '建立失敗，請稍後再試')
        this.isProcessing = false
      }
    },
    toastFire(action, text) {
      Toast.fire({
        icon: action,
        title: text,
        background: '#eee',
        showConfirmButton: false,
      })
    },
  },
  beforeRouteUpdate(to, from, next) {
    const { id } = to.params
    this.fetchData(id)
    next()
  },
  created() {
    const { id } = this.$route.params
    this.fetchData(id)
  },
}
</script>
